<template>
  <div class="block2">
    <h1>b页面</h1>
    <el-checkbox v-model="checked">A 页面在菜单组件的是否隐藏</el-checkbox>
<!--    <button v-on:="obj">123</button>-->
  </div>
</template>

<script>
// import
export default {
  name: 'B',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 控制 A 页面在菜单组件的显隐
      checked: false,
      obj: {
        click () {
          console.log('click')
        },
        hover () {
          console.log('hover')
        }
      }
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {},
  beforeRouteEnter (to, from, next) {
    console.log('beforeRouteEnter')
    next()
  },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
    console.log('beforeUpdate')
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {
    checked (val) {
      this.$store.commit('permission/changeRoute', {id: 2, obj: {hidden: val}})
    }
  },
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
  }
}
</script>

<style scoped>

</style>



